import PropTypes from "prop-types"
import React from "react"

import Nav from "react-bootstrap/Nav"
import Navbar from "react-bootstrap/Navbar"

const activeStyle = {
  marginRight: `1rem`,
  color: `white`,
  font: `normal normal 130% Comic Neue`,
  float: `right`,
  textDecoration: `underline`,
}

const normalStyle = {
  marginRight: `1rem`,
  color: `white`,
  font: `normal normal 130% Comic Neue`,
  float: `right`,
}

function isActive(word) {
  if (typeof window !== "undefined") {
    const homeURL = window.location.origin + "/"
    if (homeURL === window.location.href) {
      switch (word) {
        case "projects":
        case "blogs":
        case "contact":
          return normalStyle
        default:
          return activeStyle
      }
    }
    return window.location.href.includes(word) ? activeStyle : normalStyle
  } else return normalStyle
}

const NavLink = props => (
  <Nav.Link style={isActive(props.page)} href={props.to}>
    {props.children}
  </Nav.Link>
)

const Header = ({ siteTitle }) => (
  <header
    style={{
      background: `#333300`,
      marginBottom: `1.45rem`,
    }}
  >
    <Navbar
      collapseOnSelect
      expand="lg"
      variant="dark"
      style={{
        margin: `0 auto`,
        maxWidth: `80%`,
        padding: `1.45rem 1.0875rem`,
      }}
    >
      <h1 style={{ margin: 0 }}>
        <Navbar.Brand
          style={{
            color: `white`,
            textDecoration: `none`,
            font: `normal bold 100% Fira Code`,
          }}
          href="/"
        >
          Ritesh Kumar
        </Navbar.Brand>
      </h1>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="mr-auto">
          <NavLink to="/" page="home">
            Home
          </NavLink>
          <NavLink to="/projects/" page="projects">
            Projects
          </NavLink>
          <NavLink to="/blogs/" page="blogs">
            Blogs
          </NavLink>
          <NavLink to="/contact/" page="contact">
            Contact
          </NavLink>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
